.player-container{
  width: 100%;
  height: 85vh;
  display: flex;
}

.player-container .side-bar{
  width: 20%;
  height: 100%;
  background: linear-gradient(transparent, rgba(0,0,0,1));
  background-color: #141414;
}

.player-container .player-playlist{
  width: 80%;
  height: 100%;
  background: linear-gradient(transparent, rgba(0,0,0,1));
  background-color: rgba(32, 87, 100);
}

.footer{
  width: 100%;
  height: 10vh;
  background-color: #141414;
}